<template lang="html">
  <div class="bind-bankcard-page">
    <!--  -->
    <default-head header="" path="/accountmanage"></default-head>
    <form-line-light label=":" width="1.68rem" v-model="bankInfo.realName" :disabled="true"
      data-vv-name="" v-validate="'required'" name="realName"
     :borderB="true"></form-line-light>
    <form-select label=":" :borderB="true" width="1.68rem" v-model="bankInfo.bankId" data-vv-name="" v-validate="'required'" name="bank"
      :bankList="bankList"></form-select>
    <form-line-light label=":" placeholder="" width="1.68rem" v-model="bankInfo.dot"
    data-vv-name="" v-validate="'required'" name="dot" :borderB="true"></form-line-light>
    <form-line-light label=":" placeholder="" width="1.68rem" v-model="bankInfo.cardNo"
    data-vv-name="" v-validate="'required'" name="cardNo"
    ></form-line-light>
    <p class="warn" v-if="$route.query.isBank != 1">: !</p>
    <sub-button v-if="$route.query.isBank != 1" text="" marginTop="1rem" @click.native="toBindcard"></sub-button>

  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import lightTitle from '@/components/common/lightTitle'
import formSelect from '@/components/common/formSelect'
import formLine from '@/components/common/formLine'
import formLineLight from '@/components/common/formLineLight'
import subButton from '@/components/common/subButton'

export default {
  components: { defaultHead, lightTitle, formSelect, formLine, formLineLight, subButton },
  data () {
    return {
      bankList: [],
      bankInfo: {
        userId: utils.userId(),
        bankId: '',
        cardNo: '',
        dot: '',
        realName: '',
        userType: utils.getType()
      }
    }
  },
  computed: {
    userId () {
      return utils.userId()
    },
    userType () {
      return utils.getType()
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      minutils.IndicatorOpen('...')
      // this.ajaxGetBankList({userId: this.userId, ifSuccess: true})
      if (this.userType === 0) {
        this.ajaxGetUserInfo({userId: this.userId})
      } else {
        this.ajaxGetCompanyInfo({userId: this.userId})
      }
    },
    isRecognize (path, message) {
      let self = this
      minutils.MessageBoxConfirm({
        message: message + ', !',
        options: {confirmButtonText: '', cancelButtonText: ''},
        success (action) {
          self.$router.push(path)
        },
        fail (action) {
          self.$router.go(-1)
        }
      })
    },
    toBindcard () {
      this.$validator.validateAll().then(() => {
        this.ajaxBindBankCard(this.bankInfo)
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    ajaxBindBankCard (data) {
      api.bindBankCard(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.$router.push({path: 'bindbankcardresult', query: {realName: encodeURI(this.bankInfo.realName || '')}})
        }
      })
    },
    ajaxGetUserInfo (data) {
      api.getUserInfo(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          if (result.data.status !== 3) {
            minutils.IndicatorClose()
            let path = '/personrecognize'
            let message = ''
            this.isRecognize(path, message)
            return
          } else {
            this.bankInfo.realName = result.data.realName || result.data.legalName
            this.ajaxGetBankList({userId: this.userId, userType: this.userType, ifSuccess: true})
          }
        }
      })
    },
    ajaxGetCompanyInfo (data) {
      api.getCompanyInfo(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          if (result.data.status !== 3) {
            minutils.IndicatorClose()
            let path = 'companyrecognize'
            let message = ''
            this.isRecognize(path, message)
            return
          } else {
            this.bankInfo.realName = result.data.realName || result.data.name
            this.ajaxGetBankList({userId: this.userId, userType: this.userType, ifSuccess: true})
          }
        }
      })
    },
    ajaxGetUserAllBankCard (data) {
      api.getUserBankCard(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess && result.data.length > 0) {
          Object.keys(this.bankInfo).forEach(k => {
            if (result.data[0][k]) {
              this.bankInfo[k] = result.data[0][k]
            }
          })
          this.bankInfo.id = utils.userId()
        }
      })
    },
    ajaxGetBankList (data) {
      api.getBankList(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.bankList = result.data
          this.ajaxGetUserAllBankCard(data)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.bind-bankcard-page {
  padding-top: .88rem;
  .warn {
    color: @redColor;
    font-size: .3rem;
    line-height: .3rem;
    padding: .2rem 0 0 .22rem;
  }
}

</style>



